seajs.use([], function (UserBillday,Util) {
    var index = {
        //顶部广告位轮播
        bannerSwipe: function(){
            var swipe = new Swipe($("#banner_list").get(0), {
                startSlide: 0,
                speed: 400,
                auto: 4000,
                continuous: false,
                disableScroll: false,
                stopPropagation: false,
                callback: function (index, elem) {
                    var img = $(elem).find('img'),
                        src = img.data('src');
                    if (!!src) {
                        img.attr('src', src).removeData('src');
                    }
                },
                transitionEnd: function (index, elem) {
                    $('#banner_indicator_list .js-banner-indicator').removeClass('on')
                        .eq(index).addClass('on');
                }
            });

            this.renderIndicator('#banner_list .js-banner-item', '#banner_indicator_list');
        },
        //底部猜你喜欢的轮播
        guessYouLikeSwipe: function(){
            var swipe = new Swipe($("#sku_list").get(0), {
                startSlide: 0,
                speed: 400,
                auto: 4000,
                continuous: false,
                disableScroll: false,
                stopPropagation: false,
                callback: function (index, elem) {
                },
                transitionEnd: function (index, elem) {
                    $('#sku_indicator_list .js-banner-indicator').removeClass('on')
                        .eq(index).addClass('on');
                }
            });

            this.renderIndicator('#sku_list .js-banner-item', '#sku_indicator_list');
        },
        //渲染轮播指示器
        renderIndicator: function(selector, indicator){
            var banner_len = $(selector).size();
            if(banner_len > 1){
                $(indicator).append(new Array(banner_len + 1).join('<span class="js-banner-indicator"></span>'))
                    .find('.js-banner-indicator').first().addClass('on');
            }
        },
        bindEvent: function() {
            //轮播初始化
            this.bannerSwipe();
            this.guessYouLikeSwipe();

        },
        init: function(){
            this.bindEvent();
        }
    };
    index.init();
});